</template>
<script>
    import { mapMutations,mapActions  } from 'vuex'
    export default { 
        
        methods: {
            hideModal(){
                this.SET_showModal(false)
            },
            ...mapMutations([
            'SET_showModal', // map `this.increment()` to `this.$store.commit('increment')`

            'SET_showUserCard', // map `this.incrementBy(amount)` to `this.$store.commit('incrementBy', amount)`

            // `mapMutations` also supports payloads:
            'SET_typeModal' // map `this.incrementBy(amount)` to `this.$store.commit('incrementBy', amount)`
            ]),
            ...mapActions([
            'showAllUsers', // map `this.increment()` to `this.$store.dispatch('increment')`
            'showUserCard', // map `this.increment()` to `this.$store.dispatch('increment')`

            ]),
            hideLanding(){
                if (this.$store.getters.getuserInfo(this.$refs.search.value)) {
                    this.showUserCard(this.$refs.search.value)
                }else{
                    console.log('something went wrong')
                }
            },

        },
        data(){
            return {
                op:0
            }
        }
    }
</script>
